import "./styles.css";
import React from "react";

export default function App() {
  const [user, setUser] = React.useState();

  React.useEffect(() => {
    fetch("http://25.69.115.167:8080/api/v1/bot")
      .then((d) => d.json())
      .then((data) => {
        console.log(data);
        setUser(data.user);
      });
  }, []);

  return (
    <div className="App">
      <h1>{user}</h1>
      <h2>Start editing to see some magic happen!</h2>
    </div>
  );
}
